import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/home.vue";
import Solution from "../components/solution.vue";
import Sample from "../components/sample.vue";
import News from "../components/news.vue";
import Contact from "../components/contact.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/solution",
    name: "solution",
    component: Solution,
    children: [
      {
        path: ":id",
        component: Solution,
      },
    ],
  },
  {
    path: "/sample",
    name: "sample",
    component: Sample,
    children: [
      {
        path: ":id",
        component: Sample,
      },
    ],
  },
  {
    path: "/news",
    name: "news",
    component: News,
    children: [
      {
        path: ":id",
        component: News,
      },
    ],
  },
  {
    path: "/contact",
    name: "contact",
    component: Contact,
    children: [
      {
        path: ":id",
        component: Contact,
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
