<template>
  <div class="news">
    <Navbar />
    <div class="banner">
      <div class="content">
        <div class="banner-t">
          <div class="banner-title">
            <h1>新闻资讯</h1>
            <h2>News</h2>
          </div>
          <p class="paragraph ffd">
            业务体系涵盖自然资源、数字中国，以及大数据和云服务等信息化平台应用，
            为国内外海量用户提供有竞争力的智慧成功案例、产品和服务，
            并致力于打造智能共享的信息化传递方式，构建资源共享的数据生态格局
          </p>
        </div>
      </div>
    </div>
    <div class="content">
      <section class="section section1">
        <ul class="list">
          <li
            class="list-item"
            v-for="(item, index) in newsList"
            :key="index"
            @click="goNews(item.url)"
          >
            <el-row>
              <el-col :span="8">
                <div class="img-wrap">
                  <img :src="item.img" alt />
                </div>
              </el-col>
              <el-col :span="16">
                <div class="news-text">
                  <h1>{{item.title}}</h1>
                  <p>{{item.profile}}</p>
                </div>
              </el-col>
            </el-row>
          </li>
        </ul>
      </section>
    </div>
    <Footerbar />
  </div>
</template>

<script>
import Navbar from "@/components/navbar.vue";
import Footerbar from "@/components/footerbar.vue";

import news from "../../public/news.json";

export default {
  name: "news",
  data() {
    return {
      activeTab: "",

      newsList: []
    };
  },
  components: {
    Navbar,
    Footerbar
  },
  created() {
    this.newsList = [...news];
    const firstIndex = this.newsList.reverse().findIndex(v => v.first);
    const newsFirst = this.newsList.splice(firstIndex, 1)[0];
    this.newsList.unshift(newsFirst);
  },
  methods: {
    goNews(v) {
      window.open(v, "_blank");
    }
  }
};
</script>

<style scoped lang="scss">
.banner {
  background: url("../assets/img/bg11.png") center top/cover no-repeat;
}
.section {
  width: 1200px;
  margin: 0 auto;
  padding-top: 30px;
  margin-bottom: 50px;

  .list-item {
    padding-top: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #dfdfdf;
    cursor: pointer;
    .img-wrap {
      height: 200px;
      box-shadow: 1px 1px 9px 0 #d0d0d0;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .news-text {
      padding-left: 30px;
      h1 {
        font-size: 20px;
        margin-bottom: 20px;
        letter-spacing: 1px;
      }

      p {
        line-height: 1.5;
        font-size: 16px;
        color: #586069;
      }
    }

    &:hover {
      background-color: #f3f6f8;
    }
  }
}
</style>
